h1 {
    font-weight: 700;
    font-size: 2em;
    padding: 10px 0px; }

h2 {
    font-weight: 700;
    font-size: 1.6em;
    letter-spacing: -0.1em;
    padding: 10px 0px 0px 0px; }


h3 {
    font-weight: 500;
    font-size: 1.0em; }

h4 {
    font-weight: 300;
    font-size: 0.8em; }

p {
    font-weight: 400;
    padding-left: 0em;
    letter-spacing: -0.01em;
    line-height: 1.4em; }

@mixin ease($time) {
    transition: $time ease; }

.cv_section {
    font-weight: 500;
    padding: 0px 0px 20px 0px;

    .description {
        opacity: 0;
        padding: 0px;
        height: 0px;
        @include ease(0.2); } }

.cv_item:hover {
    .description {
        opacity: 1;
        padding: 10px 0px;
        height: 30px;
        @include ease(0.2s); } }

.school {
    line-height: 1.0em;
    padding: 20px 20px;
    opacity: 0.75; }

.school:hover {
    opacity: 1; }


.degree {
    font-size: 1em;
    display: inline-block; }

.degree_date {
    float: right;
    display: inline-block; }

.school_name {
    font-size: 0.8em; }
