@import "../../../main.sass";

.lattice {
    display: block;
    min-width: 550px;
    width: 100%;
    padding: 0px; }


.row {
    width: 100%;
    display: table-row; }

.cell {
    display: table-cell;
    border-width: 1px;
    border-color: #222;
    border-style: solid;
    span {
        display: block;
        width: 16px;
        height: 16px; } }

.state0 {
    background-color: #3dcc97; }


.state1 {
    background-color: #2f75e0;
    //background-color: #ee7d7d
 }    //#afe6bf

.state2 {
    background-color: #222; }

.state3 {
 }    // afe6bf

.pause_button {
    opacity: 0.3;
    font-size: 0.85em; }

.pause_button:hover {
    opacity: 0.7; }

.pause_button:active {
    opacity: 0.9; }
